import { useState, useRef, useEffect } from 'react';

const CardStackCollection = () => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [hoveredCard, setHoveredCard] = useState(null);
  const [scrollY, setScrollY] = useState(0);
  
  // Using useRef to store random offsets and rotations once
  const cardOffsets = useRef([]);

  // Initialize random offsets and rotations when the component mounts
  useEffect(() => {
    if (cardOffsets.current.length === 0) {
      cardOffsets.current = collections.map(() => ({
        offsetX: (Math.random() - 0.5) * 20, // -10px to 10px
        offsetY: (Math.random() - 0.5) * 10, // -5px to 5px
        rotation: (Math.random() - 0.5) * 15, // -7.5deg to 7.5deg
      }));
    }
  }, []);

  // Handle scroll events
  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      const windowHeight = window.innerHeight;
      const documentHeight = document.documentElement.scrollHeight;
      
      setScrollY(currentScrollY);
      
      // Calculate how close we are to the bottom (threshold: 80% scrolled)
      const scrollProgress = currentScrollY / (documentHeight - windowHeight);
      const shouldExpand = scrollProgress > 0.8;
      
      setIsExpanded(shouldExpand);
    };

    window.addEventListener('scroll', handleScroll);
    
    // Initial check
    handleScroll();
    
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const collections = [
    { id: 1, title: 'Poster 1', src: '/images/poster1.jpg', alt: 'Poster 1' },
    { id: 2, title: 'Poster 2', src: '/images/poster2.jpg', alt: 'Poster 2' },
    { id: 3, title: 'Poster 3', src: '/images/poster3.jpg', alt: 'Poster 3' },
    { id: 4, title: 'Poster 4', src: '/images/poster4.jpg', alt: 'Poster 4' },
    { id: 5, title: 'Poster 5', src: '/images/poster5.jpg', alt: 'Poster 5' },
    { id: 6, title: 'Poster 6', src: '/images/poster6.jpg', alt: 'Poster 6' },
  ];

  const getCardStyle = (index, total) => {
    const baseZIndex = total - index; 

    if (!isExpanded) {
      const { offsetX, offsetY, rotation } = cardOffsets.current[index] || {offsetX: 0, offsetY: 0, rotation: 0};

      return {
        transform: `translateX(${offsetX + index * 1}px) translateY(${offsetY + index * -0.5}px) rotate(${rotation}deg)`,
        zIndex: baseZIndex, 
        transition: 'all 0.8s cubic-bezier(0.4, 0, 0.2, 1), z-index 0.5s ease-out'
      };
    } else {
      const spreadDistance = 200;
      const translateX = (index - (total - 1) / 2) * spreadDistance;
      const angle = (index - (total - 1) / 2) * 3;
      
      return {
        transform: `translateX(${translateX}px) translateY(0px) rotate(${angle}deg)`,
        zIndex: hoveredCard === index ? baseZIndex + 1000 : baseZIndex + 500,
        transition: 'all 1s cubic-bezier(0.34, 1.56, 0.64, 1), z-index 0.3s ease-in-out'
      };
    }
  };

  const handleCardHover = (index) => {
    if (isExpanded) {
      setHoveredCard(index);
    }
  };

  const handleCardLeave = () => {
    setHoveredCard(null);
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <div className="min-h-[300vh] bg-gradient-to-br from-gray-50 to-gray-100">
      {/* Content sections to enable scrolling */}
      <div className="h-screen flex flex-col items-center justify-center">
        <h1 className="text-6xl font-bold text-gray-800 mb-4">Welcome</h1>
        <p className="text-xl text-gray-600 mb-8">Scroll down to see the magic happen</p>
        <div className="animate-bounce">
          <svg className="w-8 h-8 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
          </svg>
        </div>
      </div>
      
      <div className="h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-purple-50">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-gray-800 mb-4">Keep Scrolling</h2>
          <p className="text-lg text-gray-600">The cards will expand when you reach the bottom</p>
        </div>
      </div>

      {/* Card Stack Section - Fixed position */}
      <div className="fixed inset-0 flex flex-col items-center justify-center overflow-hidden pointer-events-none">
        {isExpanded && (
          <div className="text-center mb-8 opacity-0 animate-slide-down pointer-events-auto">
            <h1 className="text-4xl font-bold text-gray-800 mb-2">
              The Ultimate
            </h1>
            <h2 className="text-5xl font-black text-gray-900 mb-6">
              COLLECTIONS
            </h2>
            
            <div className="flex justify-center gap-4 mb-6">
              <span className="px-3 py-1.5 bg-cyan-400 text-white rounded-full text-xs font-medium">
                for everyone
              </span>
              <span className="px-3 py-1.5 bg-orange-400 text-white rounded-full text-xs font-medium">
                new styles
              </span>
            </div>
          </div>
        )}

        <div className="relative flex-1 flex items-center justify-center">
          <div className={`relative transition-all duration-1000 ${isExpanded ? 'w-full' : 'w-64'}`}>
            <div className="relative h-80 flex items-center justify-center">
              {collections.map((collection, index) => (
                <div
                  key={collection.id}
                  className={`absolute w-48 h-72 rounded-2xl shadow-xl cursor-pointer overflow-hidden pointer-events-auto ${hoveredCard === index ? 'scale-105 shadow-2xl' : ''}`}
                  style={getCardStyle(index, collections.length)}
                  onMouseEnter={() => handleCardHover(index)}
                  onMouseLeave={handleCardLeave}
                >
                  <img
                    src={collection.src}
                    alt={collection.alt}
                    className="w-full h-full object-cover rounded-2xl"
                  />
                </div>
              ))}
            </div>
          </div>
        </div>

        {isExpanded && (
          <div className="text-center pb-8 opacity-0 animate-slide-up pointer-events-auto">
            <p className="text-gray-600 text-sm max-w-md mx-auto mb-6">
              We Go First Choice Our Collections 12 Sessions We Create
              New Models Every Week. And We In A Few Items.
            </p>
            
            <button
              onClick={scrollToTop}
              className="px-6 py-2.5 bg-black text-white rounded-full font-medium hover:bg-gray-800 transition-colors duration-300 shadow-lg hover:shadow-xl"
            >
              Back to Top
            </button>
          </div>
        )}
      </div>

      {/* Final section */}
      <div className="h-screen flex items-center justify-center bg-gradient-to-br from-green-50 to-teal-50">
        <div className="text-center">
          <h2 className="text-4xl font-bold text-gray-800 mb-4">You Made It!</h2>
          <p className="text-lg text-gray-600">The cards should be expanded now</p>
        </div>
      </div>

      <style jsx>{`
        @keyframes slide-down {
          0% { 
            opacity: 0; 
            transform: translateY(-30px); 
          }
          100% { 
            opacity: 1; 
            transform: translateY(0); 
          }
        }
        
        @keyframes slide-up {
          0% { 
            opacity: 0; 
            transform: translateY(30px); 
          }
          100% { 
            opacity: 1; 
            transform: translateY(0); 
          }
        }
        
        .animate-slide-down {
          animation: slide-down 0.8s ease-out 0.5s forwards;
        }
        
        .animate-slide-up {
          animation: slide-up 0.8s ease-out 0.5s forwards;
        }
      `}</style>
    </div>
  );
};

export default CardStackCollection;